<template>
  <v-dialog v-model="isOpen" persistent width="800px">
    <v-card>
      <v-card-title>
        <span class="headline">Discover Points</span>
      </v-card-title>
      <v-card-text class="text-start">
        <v-container>
          <v-expansion-panels :value="panel" multiple>
            <v-expansion-panel v-for="(key, index) in Object.keys(discoverPointsResult)" :key="index">
              <v-expansion-panel-header>
                <h3>
                  {{ toUpperTextKey(key) }}
                  <v-divider inset vertical></v-divider>
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn x-small class="mr-1 btn-height-22 white--text" color="downloadColor" v-bind="attrs" v-on="on">Download<v-icon x-small right>mdi-download</v-icon> </v-btn>
                    </template>
                    <v-list>
                      <v-list-item link>
                        <v-list-item-title @click="clickDownload(discoverPointsResult[key], 'json')">Json</v-list-item-title>
                      </v-list-item>
                      <v-list-item link>
                        <v-list-item-title @click="clickDownload(discoverPointsResult[key], 'excel')">Excel</v-list-item-title>
                      </v-list-item>
                      <v-list-item link>
                        <v-list-item-title @click="clickDownload(discoverPointsResult[key], 'csv')">CSV</v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </h3>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-data-table
                  :headers="discoverPointsHeaders"
                  :items="discoverPointsResult[key]"
                  class="elevation-1"
                  v-model="selectedAddPoints"
                  show-select
                  item-key="point_name"
                  :items-per-page="5"
                  dense
                ></v-data-table>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn text @click="onClose">Close</v-btn>
        <v-btn color="primary" text @click="saveDiscoverPoints">Discover</v-btn>
        <v-btn color="nubeGreen" text @click="onSaveAdd(selectedAddPoints)" v-if="selectedAddPoints.length > 0">Add</v-btn>
      </v-card-actions>
    </v-card>
    <v-overlay :value="discoverSaving">
      <v-progress-circular indeterminate size="64" color="primary"></v-progress-circular>
    </v-overlay>
  </v-dialog>
</template>

<script>
import axios from '../../axios';
import { toUpperTextKey, copyToClipboard, exportJSON, exportCSV, exportExcel,  } from '../../utils/utils';
import { DISCOVER_POINT_HEADERS } from './bacnetMasterConstant';
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';

export default {
  props: ['on-close', 'is-open', 'device_uuid', 'on-save-add'],
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    discoverPointsHeaders: [...DISCOVER_POINT_HEADERS],
    discoverPointsResult: {},
    user_options_discover_points: {},
    panel: [],
    selectedAddPoints: [],
    discoverSaving: false,
  }),
  watch: {
    isOpen(val) {
      if (val) this.dataInit();
    },
  },
  methods: {
    dataInit() {
      this.discoverPointsResult = {};
      this.user_options_discover_points = { add_points: false };
      this.selectedAddPoints = [];
    },
    async saveDiscoverPoints() {
      try {
        this.discoverSaving = true;
        const result = await axios.post(`bacnet_master/b/device/discover/points/${this.device_uuid}`, this.user_options_discover_points);
        let count = 0;
        Object.keys(result).forEach(key => {
          if (typeof result[key] === 'object' && result[key] !== null) {
            if (result[key].points) {
              let points = [];
              Object.keys(result[key].points).forEach(type => {
                const pointsByType = result[key].points[type];
                pointsByType.map(point => {
                  point.point_object_type = type;
                  point.device_uuid = this.device_uuid;
                  count++;
                  return point;
                });
                points = points.concat(pointsByType);
              });
              this.discoverPointsResult = { ...this.discoverPointsResult, [key]: points };
              
            }
          }
        });
        this.panel = Object.keys(this.discoverPointsResult).map((k, i) => i);
        count == 0 ? this.$toasted.show(JSON.stringify(result), { type: 'error' }) : this.$toasted.show(`Discovered ${count} points`, { type: 'success' });
        return this.discoverSaving = false;
      } catch (error) {
        this.$toasted.show(err, { type: 'error' });
        return this.discoverSaving = false;
      }
    },
    clickJsonExport(data) {
      const status = copyToClipboard(JSON.stringify(data));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(data, type) {
      if (type === 'csv') {
        return exportCSV('discover-points', data);
      }
      if (type === 'excel') {
        return exportExcel('discover-points', data);
      }
      if (type === 'json') {
        return exportJSON('discover-points', JSON.stringify(data));
      }
    },

  },
};
</script>


